<template>
   <div class="wrapper">
    <Navbar
        @setSearchVal="search = $event"
    />
    <Notes 
        :notes="filterNotes"
        @delNote="delNote"
        @changeNote="changeNote"
    />
    <Modal 
        v-show="isModalOpen"
        @closeModal="isModalOpen = false, edit = false"
        @addNote="addNote"
        @editeNewNote="editeNewNote"
        :edit="edit"
        :editedNote="editedNote"
    />
    <button v-show="!isModalOpen" @click="isModalOpen = true, edit = false" class="add__note">
        <img src="@/assets/images/edit.svg" alt="">
    </button>
   </div>
</template>

<script>

import Navbar from '@/components/Navbar.vue'
import Notes from '@/components/Notes.vue'
import Modal from '@/components/Modal.vue'

export default {

    components: {
        Navbar,
        Notes,
        Modal
    },

    data() {
        return {
            search: '',
            edit: false,
            isModalOpen: false,
            currentId: 1,
            notes: [],
            editedNote: null
        }
    },

    mounted() {
        this.getNotes()
        this.notes.length > 0 ? this.currentId = (this.notes[(this.notes.length - 1)].id + 1) : this.currentId = 1
    },

    computed: {
        filterNotes() {
            return this.search ? this.notes.filter(note => note.title.toLowerCase().includes
                (this.search.toLowerCase())) : this.notes
        }
    },

    methods: {
        addNote(note) {
            note.id = this.currentId++
            this.notes.push(note)
            this.isModalOpen = false
        },

        delNote(note) {
            const idx = this.notes.findIndex(element => element.id === note.id)
            this.notes.splice(idx, 1)
        },

        getNotes() {
            const localNotes = localStorage.notes

            if(localNotes) {
                this.notes = JSON.parse(localNotes)
            }
        },

        changeNote(note) {
            this.isModalOpen = this.edit = true
            const currentNote = this.notes.find(element => element.id == note.id)
            this.editedNote = currentNote;
        },

        editeNewNote(editedNote) {
            this.notes.forEach(note => {
                if(note.id === editedNote.id) {
                    note.title = editedNote.title;
                    note.text = editedNote.text;
                    note.date = editedNote.date;
                }
            })
        }
    },

    watch: {
        notes: {
            handler() {
                localStorage.notes = JSON.stringify(this.notes)
            },
            deep: true
        }
    } 
}
</script>